import { BrowserModule } from '@angular/platform-browser';
import { NgModule, Component } from '@angular/core';
import {FormsModule} from '@angular/forms'
import {RouterModule, Routes} from '@angular/router'
import { AppComponent } from './app.component';
import { LoginComponent } from './components/login/login.component';
import { RegisterComponent } from './components/register/register.component';
import { DashboardComponent } from './components/dashboard/dashboard.component';
import { AuthService } from './services/auth.service';
import {HttpModule} from "@angular/http";
import { FlashMessagesModule } from 'ngx-flash-messages';
import { ProfileComponent } from './components/profile/profile.component';
import { AttendanceComponent } from './components/attendance/attendance.component';
import { CountdownTimerModule } from 'ngx-countdown-timer';
//import { Ng2GoogleChartsModule } from 'ng2-google-charts';
import { MbscModule } from "@mobiscroll/angular-lite";
import { TakeattendanceComponent } from './components/takeattendance/takeattendance.component';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
    import { CountDownModule } from 'ng2-analogical-countdown';
    import {AuthGuard} from  "./services/auth-guard.service";
import { GenattendanceComponent } from './components/genattendance/genattendance.component';
import { MembersComponent } from './components/members/members.component';
import { ChangepasswordComponent } from './components/changepassword/changepassword.component'
const app:Routes = [
  {path:'', pathMatch:'full' ,redirectTo:'login'},
  {path:"login",component:LoginComponent},
  {path:"register",component:RegisterComponent},
  {path:"dashboard",component:DashboardComponent,canActivate: [AuthGuard]},
  {path:'profile',component:ProfileComponent,canActivate: [AuthGuard]},
  {path:'attendance',component:AttendanceComponent,canActivate: [AuthGuard]},
  {path:"takeattendance",component:TakeattendanceComponent,canActivate: [AuthGuard]},
  {path:"genattendance",component:GenattendanceComponent,canActivate: [AuthGuard]},
  {path:"members",component:MembersComponent,canActivate: [AuthGuard]},
  {path:"changepassword",component:ChangepasswordComponent,canActivate: [AuthGuard]}
]
@NgModule({
  declarations: [
    AppComponent,
    LoginComponent,
    RegisterComponent,
    DashboardComponent,
    ProfileComponent,
    AttendanceComponent,
    TakeattendanceComponent,
    GenattendanceComponent,
    MembersComponent,
    ChangepasswordComponent
  ],
  imports: [
    BrowserModule,
    RouterModule.forRoot(app),
    FormsModule,
    HttpModule,
    MbscModule,
    //Ng2GoogleChartsModule,
    FlashMessagesModule,
    CountdownTimerModule.forRoot(),
    BrowserModule, 
    BrowserAnimationsModule, 
    CountDownModule
  ],
  providers: [AuthService,AuthGuard],
  bootstrap: [AppComponent]

})
export class AppModule { }



