import { Component, OnInit } from '@angular/core';
import { AuthService } from '../../services/auth.service';
import { FlashMessagesModule, FlashMessagesService } from 'ngx-flash-messages';

@Component({
  selector: 'app-changepassword',
  templateUrl: './changepassword.component.html',
  styleUrls: ['./changepassword.component.css']
})
export class ChangepasswordComponent implements OnInit {
      password:String 
      npassword1:String
      npassword2:String
      status:any
      regno:any
      id:String
  constructor(private auth:AuthService,private flashMessagesService:FlashMessagesService) { }

  ngOnInit() {
    this.gettoken()
  }
     change(){
       const details = ({
         password:this.password,
         npassword1:this.npassword1,
         npassword2:this.npassword2
       })
       const details1 =({
        password:this.password,
        npassword:this.npassword1,
        regno:this.regno,
        id:this.id
       })
       console.log(details1)
      if(!this.auth.validatechange(details)){
        this.flashMessagesService.show('All fields are required ', {
          classes: ['alert', 'alert-danger'], // You can pass as many classes as you need
          timeout: 1000, // Default is 3000
        });   
        return false 
      }
      else {

        //we compare the two password entered if they are the same 
        if(details.npassword1 == details.npassword2){
            this.auth.changepassword(details1).subscribe(data=>{
              if(data.success == true){
                this.flashMessagesService.show(data.msg, {
                  classes: ['alert', 'alert-success'], // You can pass as many classes as you need
                  timeout: 1000, // Default is 3000
                });   
                this.password = null
                this.npassword1 =null
                this.npassword2 = null
              }
              if(data.success == false){
                this.flashMessagesService.show(data.msg, {
                  classes: ['alert', 'alert-danger'], // You can pass as many classes as you need
                  timeout: 1000, // Default is 3000
                });   
              }
            })
        }
              else{
                this.flashMessagesService.show(' Password Doesnt Match ', {
                  classes: ['alert', 'alert-danger'], // You can pass as many classes as you need
                  timeout: 1000, // Default is 3000
                });   
              }
      }
     }

     gettoken(){
  
      const token = localStorage.getItem("token")
      this.auth.submittoken(token).subscribe(data=>{
       // //console.log(data.attendance[0].service_date)
        this.regno = data.regno
        this.id = data._id

       
         
       
        
       ////console.log(this.user.attendance)
       
        
       
       
        
      })
     }
}



