import { Component, OnInit } from '@angular/core';
import { AuthService } from '../../services/auth.service';
import {members} from './members'
import { FlashMessagesService } from 'ngx-flash-messages';

@Component({
  selector: 'app-members',
  templateUrl: './members.component.html',
  styleUrls: ['./members.component.css']
})
export class MembersComponent implements OnInit {

  level:Number
  members:members
  status:Number

  constructor(private api:AuthService,private flashMessagesService: FlashMessagesService) { }

  ngOnInit() {
this.gettoken()
  }
     getdetails(){
      
           const details = ({
             level:this.level
           })
           console.log(details)
         this.api.getmemberlevel(details).subscribe(data=>{
           this.members = data
           console.log(this.members)
         })
     }

     delete(id){
         const details = ({
           id:id
         })
         console.log(details)
        this.api.deletemember(details).subscribe(data=>{
          if(data.success == 'true'){
           
            this.flashMessagesService.show(data.msg, {
              classes: ['alert', 'alert-success'], // You can pass as many classes as you need
              timeout: 1000, // Default is 3000
            });

          
              
          }
        })
        this.getdetails()
     }

     gettoken(){
  
      const token = localStorage.getItem("token")
      this.api.submittoken(token).subscribe(data=>{
       // //console.log(data.attendance[0].service_date)
        this.status = data.status
     
       
        
       ////console.log(this.user.attendance)
       
        
       
       
        
      })
     }
     print(): void {
      let printContents, popupWin;
      printContents = document.getElementById('print-section').innerHTML;
      popupWin = window.open('', '_blank', 'top=0,left=0,height=100%,width=auto');
      popupWin.document.open();
      popupWin.document.write(`
        <html>
          <head>
            <title>GHM DataBase(Developed by SteveInc)</title>
           
            <link rel="stylesheet" type:"text/css" href="assets/css/bootstrap.min.css">
            
          </head>
      <body onload="window.print();window.close()">${printContents}</body>
        </html>`
      );
      popupWin.document.close();
  }
}



